class SectionImages extends React.Component {
  constructor(props) {
    super(props);
    this.state = { items: [] };
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  getSectionImages = () => {
    fetch(`${BACKEND_URI}/siteContent`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        site: "ozerodom.ru"
      })
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          items: response.carousel.map((image, index) => {
            return { id: `item-${index}`, content: image };
          })
        });
        // console.log(response[0][Object.keys(response[0])[0]])} //getting Images
        // this.setState({images:response})
      })
      .then(() => console.log(this.state.images));
    // .renderNewPost(response))
    // .then(() => this.props.onInsertionSuccess())
    // .then(() => this.props.onResetInsertPostFields());
  };
  onDragEnd(result) {
    // dropped outside the list
    if (!result.destination) {
      return;
    }

    const items = reorder(
      this.state.items,
      result.source.index,
      result.destination.index
    );

    this.setState({
      items
    });
  }

  componentDidMount() {
    this.getSectionImages();
  }

  render() {
    // this.getSectionImages();
    // let { images } = this.state;
    let { classes } = this.props;
    // let array = images.carousel;
    return (
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Grid
          container
          direction="column"
          justify="center"
          alignItems="flex-start"
          ref={0}
        >
          <Typography variant="h5" gutterBottom>
            Фотографии слайдера
          </Typography>
          <DragDropContext onDragEnd={this.onDragEnd}>
            <Droppable droppableId="droppable">
              {(provided, snapshot) => (
                <div
                  ref={provided.innerRef}
                  style={getListStyle(snapshot.isDraggingOver)}
                >
                  {this.state.items.map((item, index) => (
                    <Draggable
                      key={item.id}
                      draggableId={item.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          <Card className={classes.card}>
                            <CardActions
                              className={classes.actions}
                              disableActionSpacing
                            >
                              <IconButton aria-label="Add to favorites">
                                <AddIcon />
                              </IconButton>
                              <IconButton aria-label="Share">
                                <DeleteIcon />
                              </IconButton>
                            </CardActions>
                            <CardMedia
                              className={classes.media}
                              image={item.content}
                              title="Лесная гавань"
                            />
                          </Card>
                        </div>
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
          {/* {console.log(images.path)} */}
          <Button>Добавить</Button>

          <Typography variant="h5" gutterBottom>
            Фотографии Генерального плана
          </Typography>
          <ReactDragList
            dataSource={["row1", "row2", "row3"]}
            row={(record, index) => <div>index + record</div>}
          />

          <Typography variant="h5" gutterBottom>
            Фотографии Инфраструктуры
          </Typography>
          <ReactDragList
            dataSource={["row1", "row2", "row3"]}
            row={(record, index) => <div>index + record</div>}
          />

          <Typography variant="h5" gutterBottom>
            Фотогаллерея
          </Typography>
          <ReactDragList
            dataSource={["row1", "row2", "row3"]}
            row={(record, index) => <div>index + record</div>}
          />

          <Typography variant="h5" gutterBottom>
            Фотографии проезда
          </Typography>
          <ReactDragList
            dataSource={["row1", "row2", "row3"]}
            row={(record, index) => <div>index + record</div>}
          />

          <Typography variant="h5" gutterBottom>
            Фотографии проектов
          </Typography>
          <ReactDragList
            dataSource={["row1", "row2", "row3"]}
            row={(record, index) => <div>index + record</div>}
          />
        </Grid>
      </main>
    );
  }
}

